.Gallery {
  padding: 50px;
  width: 100%;
  max-width: 1200px;
}
.GalleryTitle {
  font-size: 25px;
}
.Top {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.GalleryLi {
  display: inline-block;
  padding: 12px 18px;
  background-color: #fff;
  border-radius: 10px;
  margin-right: 10px;
  cursor: pointer;
  font-weight: 500;
  font-size: 18px;
  border: 1px solid transparent;
  transition: all 0.15s ease-in-out;
}
.GalleryLi:hover {
  border-color: #000;
}
.Active {
  background-color: black;
  color: #fff;
}
.Content {
  display: grid;
  grid-template-columns: repeat(3, 2fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  margin-top: 40px;
}
@media (max-width: 1000px) {
  .Content{
    grid-template-columns: repeat(2, 1fr);
  } 
}
@media (max-width: 600px) {
  .Content {
    grid-template-columns: repeat(1, 1fr);
  }
}
.searchInput {
  font-size: 16px;
  width: 250px;
  height: 50px;
  padding: 0 15px;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  outline: none;
  transition: all 0.15s ease-in-out;

}
.searchInput:focus {
  border: 1px solid rgba(0, 0, 0, 0.4);
}
.Pagination {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 40px;
}