.Search {
  position: relative;
}
.UsersInput {
  border: 1px solid rgba(0, 0, 0, 0.08);
  padding: 15px;
  border-radius: 15px;
  width: 100%;
  font-size: 16px;
  padding-left: 50px;
}
.UsersSvg {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 17px;
  left: 20px;
  opacity: 0.3;
}
.UsersList {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow: auto;
  height: calc(100% - 130px);
  margin-top: 30px;
}
.SendInviteBtn {
  background-color: rgb(251, 80, 0);
  padding: 16px 15px;
  border-radius: 15px;
  font-size: 16px;
  color: #fff;
  width: 100%;
  border: 0;
  outline: none;
  box-shadow: 0 7px 10px rgba(251, 80, 0, 0.4);
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
.SendInviteBtn:hover {
  background-color: rgb(255, 97, 24);
}
.SendInviteBtn:active {
  transform: translateY(2px);
  box-shadow: none;
}