.Counter {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  text-align: center;
}
h1 {
  margin: 0;
  margin-bottom: 20px;

  color: #000;
  font-size: 7rem;
}
h2 {
  margin: 0;

  color: #556581;
  font-size: 2rem;
}
.Button {
  cursor: pointer;
  
  margin: 0 10px;
  padding: 10px 25px;

  text-align: center;
  font-size: 22px;
  
  background: transparent;
  border: 2px solid transparent;
  border-radius: 15px;
  
  transition: all 0.05s ease-in-out;
}
.Minus {
  color: #fff;
  
  background-color: #e63946;
  border: 1px solid #c02e3b;
  box-shadow: 0 4px 0 #9c2630;
}
.Plus {
  color: #fff;
  
  background-color: #2a9d8f;
  border: 1px solid #23867b;
  box-shadow: 0 4px 0 #227e73;
}
.Button:hover {
  transform: translateY(-2px);
}

.Button:active {
  transform: translateY(2px);
  box-shadow: none;
}