.Block input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 3rem;
  font-weight: bold;
  padding: 20px 20px;
  letter-spacing: 1.3px;
  outline: none;
  margin-top: 20px;
  border-radius: 5px;
  width: 100%;
}
.Currencies {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  overflow: hidden;
}
.CurLi {
  display: flex;
  padding: 10px 20px;
  border-right: 1px solid rgba(0, 0, 0, 0.05);
  cursor: pointer;
  justify-content: center;
  flex: 1;
}
.CurLi:hover {
  background-color: rgba(0, 0, 0, 0.03);
}
.CurLi:active {
  background-color: rgba(0, 0, 0, 0.05);
}
.Active {
  background-color: #16b67f;
  color: #fff;
}
.Active:hover {
  background-color: #16a171;
  color: #fff;
}