.Header {
  z-index: 5;
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 300px;

  background-color: rgb(31, 30, 30);
}
.HeaderNavList {
  display: flex;
  gap: 30px;

  color: white;
  font-size: 20px;
}
.HeaderNavList li:hover {
  transform: translateY(-2px) rotate(5deg);
  text-decoration: underline;
}