.SuccessBlock {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex-direction: column;
  animation: success-show 0.5s cubic-bezier(0.25, 0.75, 0.5, 1.25);
}
@keyframes success-show {
  0% {
  opacity: 0;
  transform: scale(0.8);
  }
  100% {
  opacity: 1;
  transform: scale(1);
  }
}
.SuccessImg {
  width: 120px;
  height: 120px;
}
.SuccessText {
  font-size: 24px;
  margin-bottom: 0;
  margin-top: 10px;
}
.SuccessParagraph {
  text-align: center;
  margin-bottom: 30px;
}
.SendInviteBtn {
  background-color: rgb(251, 80, 0);
  padding: 16px 15px;
  border-radius: 15px;
  font-size: 16px;
  color: #fff;
  width: 200px;
  border: 0;
  outline: none;
  box-shadow: 0 7px 10px rgba(251, 80, 0, 0.4);
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
.SendInviteBtn:hover {
  background-color: rgb(255, 97, 24);
}
.SendInviteBtn:active {
  transform: translateY(2px);
  box-shadow: none;
}


