.Modal {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.OpenModalBtn {
  cursor: pointer;
  
  padding: 18px 25px;
  font-weight: 500;
  font-size: 22px;
  line-height: 23px;
  
  background: #fff;
  border: 0;
  border-radius: 20px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  
  outline: none;
  transition: all 0.1s ease-in-out;
}
.OpenModalBtn:hover {
  box-shadow: 0 8px 10px rgba(0, 0, 0, 0.1);
  transform: translateY(-1px);
}
.OpenNodalbtn:active {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  transform: translateY(0);
}