.Overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.4);

  transition: all 0.5s ease-in-out;
}

.ModalWindow {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: center;
  width: 70%;
  max-width: 500px;
  max-height: 300px;
  padding: 40px 30px;
  
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  
  transform: translate(-50%, -50%);
  transition: all 0.5s ease-in-out;
}
img {
  width: 100%;
  border-radius: 5px;
}
svg {
  position: absolute;
  right: 10px;
  top: 10px;
  height: 24px;
  width: 24px;
  
  opacity: 0.3;
}
svg:hover {
  opacity: 1;
}
.Overlay.Animated {
  opacity: 0;
  visibility: hidden;
}
.Overlay.Animated .ModalWindow{
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -30%);
  transition-delay: 0.2s;
}
.Overlay.Show {
  opacity: 1;
  visibility: visible;
}
.Overlay.Show .ModalWindow{
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%);
}