.Collection {
  max-width: 470px;
  background-color: #fff;
  padding: 30px;
  border-radius: 30px;
  box-shadow: 0 3px 0 rgba(0, 0, 0, 0.05);
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.Collection:hover {
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
  transform: translateY(-5px);
}
@media (max-width: 1000px) {
  .Collection {
    width: 100%;
  }
}
img {
  object-fit: cover;
}
.Collection__big {
  height: 250px;
  width: 100%;
  border-radius: 20px;
  margin-bottom: 15px;
}

.Collection__bottom {
  display: flex;
  justify-content: space-between;
}

.Collection__mini {
  width: 31%;
  height: 80px;
  border-radius: 20px;
}
.CollectionName {
  margin: 0;
    margin-top: 15px;
    font-size: 20px;
}