.UserItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}
.UserItem > div {
  display: flex;
}
.Avatar {
  border-radius: 30px;
  width: 50px;
  height: 50px;
  margin-right: 15px;
}
.UserName {
  margin: 0;
  font-weight: 900;
  font-family: 'Merriweather', serif;
  margin-left: 15px;
  margin-top: 5px;
  margin-left: 0;
  color: #182170;
}
.Action {
  width: 28px;
  height: 28px;
  opacity: 0.3;
  cursor: pointer;
}
.Action:hover {
  opacity: 0.8;
}
.UsersParagraph {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 14px;
  margin: 0;
  color: rgba(0, 0, 0, 0.5);
  margin-top: 3px;
}