.Quiz {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  
}
.QuizBox {
  position: relative;
  width: 500px;
  border-radius: 30px;
  padding: 40px;
  background-color: #fff;
}
.QuizBox:before,
:after {
  content: '';
  position: absolute;
  left: 50%;
  display: block;
  height: 50px;
  border-radius: 30px;
  transform: translateX(-50%);
}
.QuizBox:before {
  z-index: 0;
  bottom: -10px;
  width: 95%;
  background-color: rgba(255, 255, 255, 0.8);
}
.QuizBox:after {
  z-index: 1;
  bottom: -20px;
  width: 90%;
  background-color: rgba(255, 255, 255, 0.3);
}
.QuizTitle {
  font-size: 2rem;
}
.Progress {
  height: 10px;
  border-radius: 30px;
  background-color: rgb(232, 232, 232);
  margin-bottom: 25px;
}
.ProgressInner {
  height: 100%;
  border-radius: 30px;
  width: 80%;
  background: rgb(18, 231, 48);
  transition: all 0.3s ease-in-out;
  background: linear-gradient(90deg, rgba(18, 231, 48, 1) 0%, rgba(0, 212, 255, 1) 100%);
}
.QuizLi {
  padding: 15px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
.QuizLi:hover {
  border: 2px solid rgba(0, 0, 0, 0.3);
}